"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import { ClientHeader } from "@/components/client/client-header"
import { BottomNav } from "@/components/client/bottom-nav"
import { LoginModal } from "@/components/client/login-modal"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Separator } from "@/components/ui/separator"
import { useAuthStore } from "@/lib/store"
import { useToast } from "@/hooks/use-toast"
import { User, MapPin, Phone, Mail, LogOut, ChevronRight } from "lucide-react"

export default function ProfilePage() {
  const [isLoginModalOpen, setIsLoginModalOpen] = useState(false)
  const router = useRouter()
  const { toast } = useToast()
  const { user, logout, isAuthenticated } = useAuthStore()

  useEffect(() => {
    if (!isAuthenticated) {
      setIsLoginModalOpen(true)
    }
  }, [isAuthenticated])

  const handleLogout = () => {
    logout()
    toast({
      title: "Logout realizado",
      description: "At√© logo!",
    })
    router.push("/client")
  }

  const handleLoginSuccess = () => {
    // User successfully logged in, page will re-render with authenticated state
  }

  if (!isAuthenticated && !isLoginModalOpen) {
    return null
  }

  return (
    <div className="min-h-screen bg-background pb-24">
      <ClientHeader />
      <main className="space-y-4 px-4 py-6">
        <Card>
          <CardContent className="flex items-center gap-4 p-4">
            <div className="flex h-12 w-12 items-center justify-center rounded-full bg-primary">
              <User className="h-6 w-6 text-primary-foreground" />
            </div>
            <div>
              <h2 className="font-semibold">{user?.name}</h2>
              <p className="text-sm text-muted-foreground">{user?.email}</p>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Informa√ß√µes pessoais</CardTitle>
          </CardHeader>
          <CardContent className="space-y-3">
            <div className="flex items-center gap-3">
              <Phone className="h-5 w-5 text-muted-foreground" />
              <div>
                <p className="text-sm text-muted-foreground">Telefone</p>
                <p className="font-medium">{user?.phone}</p>
              </div>
            </div>
            <Separator />
            <div className="flex items-center gap-3">
              <Mail className="h-5 w-5 text-muted-foreground" />
              <div>
                <p className="text-sm text-muted-foreground">Email</p>
                <p className="font-medium">{user?.email}</p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Endere√ßo padr√£o</CardTitle>
          </CardHeader>
          <CardContent>
            {user?.addresses.find((addr) => addr.isDefault) ? (
              <div className="flex items-start gap-3">
                <MapPin className="h-5 w-5 text-muted-foreground" />
                <div>
                  <p className="font-medium">
                    {user.addresses.find((addr) => addr.isDefault)?.street},{" "}
                    {user.addresses.find((addr) => addr.isDefault)?.number}
                  </p>
                  <p className="text-sm text-muted-foreground">
                    {user.addresses.find((addr) => addr.isDefault)?.neighborhood},{" "}
                    {user.addresses.find((addr) => addr.isDefault)?.city} -{" "}
                    {user.addresses.find((addr) => addr.isDefault)?.state}
                  </p>
                  <p className="text-sm text-muted-foreground">
                    {user.addresses.find((addr) => addr.isDefault)?.zipCode}
                  </p>
                </div>
              </div>
            ) : (
              <p className="text-muted-foreground">Nenhum endere√ßo cadastrado</p>
            )}
          </CardContent>
        </Card>

        <Button variant="outline" className="w-full" onClick={handleLogout}>
          <LogOut className="mr-2 h-4 w-4" />
          Sair
        </Button>
      </main>
      <BottomNav />
      <LoginModal 
        isOpen={isLoginModalOpen} 
        onClose={() => setIsLoginModalOpen(false)}
        onLoginSuccess={handleLoginSuccess}
      />
    </div>
  )
}